.time{
}
.time__current{
  display: flex;
  justify-content: flex-end;
  padding: $padding;

  font-size: 24px;

  @media(max-width: 1020px){
    font-size: calc(10px + 14 * ((100vw - 320px) / 1020))
  }
}
.time__current-week {
  font-weight: 400;
}
.time__current-date {
  width: 225px;
  margin-left: 10px;
  padding-left: 10px;
  text-align: left;
  @media(max-width: 1020px){
    width: calc(110px + 110 * ((100vw - 320px) / 1020));
  }
}
.time-list {
  padding: $padding;
  display: flex;
  justify-content: space-around;
  margin-bottom: 10px;

  font-size: 24px;
  @media(max-width: 1020px){
    font-size: calc(10px + 14 * ((100vw - 320px) / 1020))
  }
}